  src: url("../fonts/alumni-sans-600.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-300.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-400.woff2") format("woff2");
}
@font-face {
